.mobile_btn {
  margin-left: 30px;
  background: transparent;
  border: none;
  box-shadow: none;
  cursor: pointer;
  font-size: 0;
  height: 16px;
  overflow: hidden;
  width: 16px;
  position: relative;
}
.mobile_btn:focus {
  outline: none;
}
.mobile_btn span {
  background: #fff;
  display: block;
  height: 2px;
  left: 0;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
.mobile_btn span:hover {
  background: #ebebeb;
}
.mobile_btn span:after {
  background-color: #fff;
  content: "";
  display: block;
  height: 2px;
  left: 0;
  position: absolute;
  width: 100%;
  bottom: -6px;
}
.mobile_btn span:after:hover {
  background: #ebebeb;
}
.mobile_btn span:before {
  background-color: #fff;
  content: "";
  display: block;
  height: 2px;
  left: 0;
  position: absolute;
  width: 100%;
  top: -6px;
}
.mobile_btn span:before:hover {
  background: #ebebeb;
}
.btn_close {
  position: absolute;
  left: 0;
  top: 8px;
  width: 20px;
  height: 20px;
  opacity: 0.7;
  cursor: pointer;
  transition: opacity .2s;
  z-index: 7;
}
.btn_close:hover {
  opacity: 1;
}
.btn_close:before,
.btn_close:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 20px;
  width: 2px;
  background-color: #fff;
}
.btn_close:before {
  transform: rotate(45deg);
}
.btn_close:after {
  transform: rotate(-45deg);
}
