  background: #fff;
  margin-left: -30px;
}
.plans .plan-starter .plan__name {
  background: #f8f9fa;
  border-bottom: 1px solid #e7e8ea;
  border-top: 1px solid #e7e8ea;
  margin-bottom: 38px;
  margin-left: 11px;
}
.plans .plan-starter .plan__cost {
  margin-bottom: 37px;
